import { LitElement, html, css } from 'lit-element';
import { getLocale } from '../appSettings.js';

class PaymentItem extends LitElement {
  
  static get properties() {
    return {
      articleId:          { type: String },
      color:              { type: String },
      name:               { type: String },
      description:        { type: String },
      itemPrice:          { type: String },
      itemPriceCurrency:  { type: String },
      itemDiscountPrice:  { type: String },
      imageUrl:           { type: String },
      imageMissing:       { type: String },
      size:               { type: String },
      itemId:             { type: String },
      hideCross:          { type: String },
      lineId:             { type: String },
      showSizeAndPrice:   { type: Boolean },
      hideSizeAndPrice:   { type: Boolean}
    };
  }

  constructor() {
    super();
    this.articleId = '';
    this.color = '';
    this.name = '';
    this.description = '';
    this.itemPrice = '';
    this.itemPriceCurrency = '';
    this.itemDiscountPrice = '';
    this.imageUrl = '';
    
    this.imageMissing = '';
    this.size = '';
    this.itemId = '';
    this.removedFromCart = '';
    this.lineId = '';
    this.showSizeAndPrice = true;
    this.hideSizeAndPrice = false;

    this.locale = getLocale().CART_ITEM;

  }

  updated(changedProperties) {
    changedProperties.forEach((oldValue, propName) => {
      
      if (propName == "showSizeAndPrice" && oldValue) {
        this.hideSizeAndPrice = true;
/*
        this.shadowRoot.getElementById('cart-item-row-size').setAttribute("class" , "cart-item-row-size-hide")
        this.shadowRoot.getElementById('cart-item-row-price').setAttribute("class" , "cart-item-row-price-hide")
        
        setTimeout(() =>
          this.hideSizeAndPrice = true
        , 1000);
*/
      } else if (propName == "showSizeAndPrice" && !this.showSizeAndPrice) {
        this.hideSizeAndPrice = true;
      }

    });
  }

  static get styles() {
    return css`
      #cart-item-container {
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      #cart-item-row-remove-item-from-cart {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-top: 5px;
        padding-bottom: 10px;
      }

      #remove-item-cross {
        height: 12px;
        width: 12px;
      }

      #cart-item-row-image {
        display: flex;
        flex-direction: column;
        justify-content: end;
        align-items: center;
      }

      #cart-item-image {
        max-width: 100%;
        border-radius: 2px;
      }

      #missing-item-image {
        max-width: calc(100% - 2px);
        border-radius: 2px;
        border: 1px solid rgba(83, 83, 83, 0.15);
      }

      .cart-item-row-size {
        display: flex;
        align-items: end;
        justify-content: center;
        font-family: HMSans-Regular;
        font-size: 12px;
        color: #222222;
        padding-top: 5px;
      }

      .cart-item-row-size-hide {
        display: flex;
        align-items: end;
        justify-content: center;
        font-family: HMSans-Regular;
        font-size: 12px;
        color: #222222;
        padding-top: 5px;

        animation: fadeout 1.0s;
        animation-fill-mode: forwards;
      }

      .cart-item-row-price {
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: HMSans-Bold;
        font-size: 12px;
        color: #222222;
      }

      .cart-item-row-price-hide {
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: HMSans-Bold;
        font-size: 12px;
        color: #222222;

        animation: fadeout 1.0s;
        animation-fill-mode: forwards;
      }

      @keyframes fadeout {
        from { opacity: 1; }
        to   { opacity: 0; }
      }

    `;
  }

  render(){
    return html`
      <div id="cart-item-container">

        <div id="cart-item-row-image">
          ${(this.imageUrl !== "") ?
              html`<img id="cart-item-image" src="${ this.imageUrl }">` :
              html`<img id="missing-item-image" src="data:image/png;base64,">`}      
        </div>

      </div>

    `;
  }

  /* ---------------------------------------------------------------------------------------------------------------- */

  removeItemFromCart(event) {
    this.shadowRoot.dispatchEvent(new CustomEvent('delete-item-from-cart', { bubbles: true, composed: true, detail: {lineId: this.lineId} }));
  }
}
customElements.define('payment-item', PaymentItem);
